function App() {
  const [alert, setAlert] = useState(null);

  const showAlert = (message, type) => {
    setAlert({
      msg: message,
      type: type
    })
    setTimeout(() => {
      setAlert(null)
    }, 2500)
  }

  return (
   <>
       {/* <Router> */}
     <Navbar />
      <Alert alert={alert} />
      <Intro />
      <Mainpage showAlert={showAlert} heading ="Text AnalyzerX"/>
        {/* <Routes>
          <Route exact path="/" element={<Mainpage/>} />
          <Route exact path="/About" element={<About/>} />
        </Routes>

       </Router> */}


   
      </>
      );
}

      export default App;
